html {
    font-size: 62.5%; // 1rem is 10px
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
    font-family: 'montserrat';
}

body {
    overflow-x: hidden;
    font-weight: normal;
    font-family: 'Lato', sans-serif;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    -webkit-tap-highlight-color: transparent;
}

.app {
    margin-top: 2rem;
    display: grid;
    justify-items: center;
}

.post-topic {
    margin: 0 4px;
}

.app-wrapper {
    display: grid;
    justify-content: center;
}

.logo-main,
.recent-posts 
{
    display: grid;
    justify-items: center;
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}


$color-blue-one: #0079FF;
$color-gray-one: #666666;
$color-gray-two:#b3b3b3;
$color-gray-three: #D8D8D8;

.no-content {
    color: $color-gray-one;
    font-size: 14px;
    line-height: 17px;
}


@import "./mixins";
@import "./home";
@import "./search-bar";
@import "./recent-posts";
@import "./results"